import React, {useEffect, useState} from "react";
import TitleCard from "../../components/Cards/TitleCard";
import {useNavigate} from "react-router-dom";
import SearchBar from "../../components/Input/SearchBar";
import {fetchRegistrationEventListAction} from "../../app/service/action/eventAction";
import {useDispatch, useSelector} from "react-redux";
import Loading from "../../components/Loading/Loading";
import {PaginationComponent} from "../../components/Pagination/PaginationComponent";
import {EyeIcon, PencilIcon, TrashIcon} from "@heroicons/react/24/outline";
import {statusSendWhatsapp} from "../../utils/Utils";

const INITIAL_OBJECT = {
    size: 10,
    page: 1,
    search: '',
}
const TopSideButtons = ({search}) => {
    const navigate = useNavigate();
    // const handleAddNew = () => {
    //     navigate(`/app/add-event/`, { replace: true });
    // };
    const handleSearch = (event) => {
        if (event.length >= 3) {
            search(event)
        } else if (event === "") {
            search(event)
        }
    }

    return (
        <div className="inline-block float-right">
            <SearchBar placeholderText={'Cari nama rider / kategori'}
                       setSearchText={handleSearch}/>
            {/*<button className="btn px-6 btn-sm normal-case btn-primary ml-3" onClick={handleAddNew}>Add New</button>*/}
        </div>
    )
}
export const EventRegistration = ({id, title}) => {
    const [requestParam, setRequestParams] = useState(INITIAL_OBJECT)
    const dispatch: any = useDispatch()

    const data = useSelector(({eventRegistrationListState}) => eventRegistrationListState.eventRegistrationList.data)
    const isLoading = useSelector(({eventRegistrationListState}) => eventRegistrationListState.eventRegistrationList.isLoading)

    useEffect(() => {
        dispatch(fetchRegistrationEventListAction(id, requestParam))
    }, [id, requestParam]);

    const handleChange = (event, value) => {
        setRequestParams({...requestParam, page: value});
        // @ts-ignore
        dispatch(fetchRegistrationEventListAction(id, requestParam));
    };
    return (
        <TitleCard title={title} topMargin={'mt-2'}
                   TopSideButtons={<TopSideButtons search={(input) => setRequestParams({
                       ...requestParam,
                       size: 10,
                       page: 1,
                       search: input
                   })}/>}>
            <div>
                {isLoading ? <Loading/> :
                    <div className="overflow-x-auto w-full">
                        <table className="table table-zebra table-pin-rows w-full">
                            <thead>
                            <tr>
                                <th>No</th>
                                <th>Rider Name</th>
                                <th>Category</th>
                                <th>Community</th>
                                <th>Send WA</th>
                                <th width={'5%'}>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                data.result ? data.result.map((value, k) => {
                                    return (
                                        <tr key={k}>
                                            <td>{k + 1}</td>
                                            <td>{value.riderName}</td>
                                            <td>{value.categoryName}</td>
                                            <td>{value.community}</td>
                                            <td>{statusSendWhatsapp(value.sendWhatsApp)}</td>
                                            <td>
                                                <button className="btn btn-square btn-ghost">
                                                    <EyeIcon className="w-5" color={'blue'}/>
                                                </button>
                                                <button className="btn btn-square btn-ghost">
                                                    <TrashIcon className="w-5" color={'red'}/>
                                                </button>
                                            </td>
                                        </tr>
                                    )
                                }) : null
                            }
                            </tbody>
                        </table>
                        <PaginationComponent data={data} handleChange={handleChange} page={requestParam.page}/>
                    </div>
                }
            </div>
        </TitleCard>
    )
}
