import React from 'react';
import {Pagination} from "@mui/material";

export const PaginationComponent = ({data, handleChange, page}: any) => {

    return (
        <div className={'pagination'}>
            <Pagination count={data.result ? data.metaData.totalPage : 0}
                        page={page}
                        color={'primary'}
                        onChange={handleChange}/>
        </div>
    )
}
